import { isString } from '@dimjs/lang';
import { verRegex } from './constant.js';

/**
 * @description
 * Validates a version string.
 *
 * @param ver - The version string to validate.
 * @returns True if the version string is valid, false otherwise.
 *
 * @docsCategory preference/utils/semver
 * @docsPage semver-validate
 * @codeFilter validate
 */
export const validate = (ver: string): boolean => {
  if (!isString(ver)) {
    return false;
  }
  const arr = ver.replace(verRegex, '').split('.');
  if (arr.length !== 3) {
    return false;
  }
  return arr.every((el) => {
    return /^\d+$/.test(el);
  });
};
